<template>
  <div id="map" style="height:100%">
    <gmap-map :center="centers" :zoom="11" map-type-id="terrain" style="width: 100%; height:100%" @click="getloca">
      <gmap-marker
        @dragend="updateMaker"
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :clickable="true"
        :draggable="true"
        @click="centers=m.position"
      ></gmap-marker>
      <!-- @click="centers=m.position" -->
    </gmap-map>
  </div>
</template>
<script type="text/javascript" src="https://res.wx.qq.com/open/js/jweixin-1.3.2.js"></script>
<script>
export default {
  data() {
    return {
      centers: { lat: 49.271954346124886, lng: -123.13050236438585 },

      markers: [
        {
          position: { lat: 49.271954346124886, lng:-123.13050236438585 },
        },
       {
          position: { lat: 49.271954346124886, lng: -123.13050236438585 },
        },
      ],

      place: null,
    };
  },

  description: "Autocomplete Example (#164)",

  mounted() {
    setTimeout(()=>{
      wx.miniProgram.navigateTo({
            url: "../Wode/wodepages/AddressManagementNew?esd=13214"
        });
    },5000)
  },

  methods: {
      getloca(event){
          this.markers[1].position.lat = event.latLng.lat()
          this.markers[1].position.lng = event.latLng.lng()
          console.log(this.markers)
          this.get()
      },
    setPlace(place) {
      this.place = place;
    },
     get() {
            //步骤一:创建异步对象
            var ajax = new XMLHttpRequest();
            //步骤二:设置请求的url参数,参数一是请求的类型,参数二是请求的url,可以带参数,动态的传递参数starName到服务端
            ajax.open('get',
              `https://maps.googleapis.com/maps/api/geocode/json?language=zh-CN&latlng=${this.markers[1].position.lat},${this.markers[1].position.lng}&key=AIzaSyBhYuu1H_BcD6hIJiIPZ5Y05K0oY63WHtw`
              );
            //步骤三:发送请求
            ajax.send();
            //步骤四:注册事件 onreadystatechange 状态改变就会调用
            ajax.onreadystatechange = function () {
              if (ajax.readyState == 4 && ajax.status == 200) {
                //步骤五 如果能够进到这个判断 说明 数据 完美的回来了,并且请求的页面是存在的
                console.log("用户返回位置", JSON.parse(ajax.responseText)); //输入相应的内容
              }
            }

    },
    setDescription(description) {
      this.description = description;
    },

    usePlace(place) {
      if (this.place) {
        var newPostion = {
          lat: this.place.geometry.location.lat(),

          lng: this.place.geometry.location.lng(),
        };

        this.center = newPostion;

        this.markers[0].position = newPostion;

        this.place = null;
      }
    },

    updateMaker: function (event) {
      console.log("updateMaker, ", event.latLng.lat());

      this.markers[0].position = {
        lat: event.latLng.lat(),

        lng: event.latLng.lng(),
      };
    },
  },
};
</script>
<style >
body,html{
    width:100%;
    height: 100%;
}
.map{
    height: 100%;
}
    *{margin: 0;padding: 0;}
</style>